import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import { Container, Grid, type Theme, useMediaQuery } from '@mui/material'

import DateForm from 'src/components/DateForm'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import ConferencesTable from 'src/content/manage/Conference/ConferencesTable'
import PageHeader from 'src/content/manage/PageHeader'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'
import ConfirmDialog from 'src/components/ConfirmDialog'
import { useState } from 'react'
import { DEFAULT_DELETE_MESSAGE } from 'src/data/cofirmDialogueMessages'
import { useDeleteConference } from 'src/hooks/api/clubConferenceHooks'
import { CONFERENCE_MESSAGE } from 'src/data/messages'

function ConferenceList() {
  const date = useRecoilValue(dateState)

  const { userInfo } = useSafeAuthUser()
  const [isShowDialog, setIsShowDialog] = useState(false)
  const [deletionIds, setDeletionIds] = useState<number[]>([])

  const { id } = useParams()
  const clubId = Number(id)
  const club = userInfo?.clubs.find(club => club.id === clubId)
  const isUpperThanSm = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))

  const deleteConferenceMutation = useDeleteConference(
    clubId,
    deletionIds,
    CONFERENCE_MESSAGE.DELETE_SUCCESS
  )
  const handleDeleteStart = (ids: number[]) => {
    setDeletionIds(ids.slice(0))
    setIsShowDialog(true)
  }
  const handleDelete = () => {
    deleteConferenceMutation.mutate()
    setIsShowDialog(false)
  }

  return (
    <>
      <Helmet>
        <title>출석 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} buttonText="행사 추가" link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg" sx={{ px: isUpperThanSm ? 2 : 0 }}>
        <DateForm club={club} />
        <Grid container direction="row" justifyContent="center" alignItems="stretch" spacing={3}>
          <Grid item xs={12}>
            <ConferencesTable date={date} handleDelete={handleDeleteStart} />
          </Grid>
        </Grid>
      </Container>

      <ConfirmDialog
        open={isShowDialog}
        handleClose={() => setIsShowDialog(false)}
        handleSubmit={handleDelete}
        confirmMessage={DEFAULT_DELETE_MESSAGE}
      />
    </>
  )
}

export default ConferenceList
