import { useEffect, useState } from 'react'
import { useQueryClient } from 'react-query'
import { useNavigate, useParams } from 'react-router'

import { Box, Button, Card, CardHeader, Divider, Typography, useMediaQuery, useTheme } from '@mui/material'
import type { GridCellParams, GridColDef, GridEventListener, GridRowSelectionModel } from '@mui/x-data-grid'
import { DataGrid } from '@mui/x-data-grid'

import { CONFERENCE_MESSAGE } from 'src/data/messages'

import { type ConferenceTest } from 'src/models/Conference'
import type { DateProps } from 'src/models/date'

import BulkActions from 'src/content/manage/Conference/BulkActions'

import { useDeleteConference, useGetConferences } from 'src/hooks/api/clubConferenceHooks'

interface ConferencesTableProps {
  className?: string
  date: DateProps
  handleDelete(ids: number[]): void
}

const ConferencesTable: React.FC<ConferencesTableProps> = ({ date, handleDelete }) => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()
  const { id } = useParams()
  const clubId = Number(id)
  const queryClient = useQueryClient()

  const [selectedConferences, setSelectedConferences] = useState<GridRowSelectionModel>([])

  const conferences = useGetConferences(clubId, date)

  useEffect(() => {
    queryClient.invalidateQueries('getConferences')
  }, [date])

  const selectedBulkActions = selectedConferences.length > 0

  const conferenceRows =
    conferences.data?.map(conference => ({
      ...conference,
      participantTarget: conference.participantTarget ?? '-',
      conferenceParticipantCount:
        conference.conferenceParticipantCount === 0 ? '-' : `${conference.conferenceParticipantCount} 명`
    })) || []

  const columns: GridColDef[] = [
    { field: 'title', headerName: '행사명', maxWidth: 250, flex: 1 },
    { field: 'participantTarget', headerName: '참여 대상', maxWidth: 250, flex: 1 },
    { field: 'conferenceParticipantCount', headerName: '참여 인원', maxWidth: 250, flex: 1 },
    { field: 'conferenceRoundCount', headerName: '진행 회차', maxWidth: 250, flex: 1 },
    {
      field: 'attendance',
      headerName: '',
      flex: 1,
      maxWidth: 250,
      sortable: false,
      filterable: false,
      renderCell: () => {
        return <Button fullWidth>회차 및 출석 관리</Button>
      }
    },
    {
      field: 'meetingNotes',
      headerName: '',
      flex: 1,
      maxWidth: 250,
      sortable: false,
      filterable: false,
      renderCell: () => {
        return <Button fullWidth>회의록 관리</Button>
      }
    }
  ]

  const columnsForXS: GridColDef[] = [
    { field: 'title', headerName: '행사명', width: 100 },
    { field: 'conferenceParticipantCount', headerName: '참여 인원', width: 100 },
    { field: 'attendance', headerName: '', width: 100, sortable: false, filterable: false },
    {
      field: 'meetingNotes',
      headerName: '',
      flex: 1,
      maxWidth: 100,
      sortable: false,
      filterable: false
    }
  ]

  const handleCellClick: GridEventListener<'cellClick'> = params => {
    if (params.field === 'attendance') {
      navigate(`/manage/${clubId}/attendance/${params.id}/check`)
      return
    }
    if (params.field === 'title') {
      navigate(`/manage/${clubId}/attendance/${params.id}`)
      return
    }
    if (params.field === 'meetingNotes') {
      navigate(`/manage/${clubId}/attendance/${params.id}/meeting`)
      return
    }
  }

  return (
    <Card>
      <Box height="55px">
        {selectedBulkActions ? (
          <Box flex={1} p={1}>
            <BulkActions selectedConferences={selectedConferences as number[]} handleDelete={handleDelete} />
          </Box>
        ) : (
          <Box display={'flex'} alignItems={'baseline'}>
            <CardHeader title="행사 목록" />
            <Typography variant={'caption'}>행사명을 클릭하면 수정이 가능해요</Typography>
          </Box>
        )}
      </Box>

      <Divider />
      <Box
        sx={{
          '& .title--bold': {
            fontWeight: 'bold',
            textDecoration: 'underline',
            cursor: 'pointer'
          }
        }}
      >
        <DataGrid
          autoHeight
          rows={conferenceRows}
          columns={matches ? columns : columnsForXS}
          initialState={{
            pagination: { paginationModel: { pageSize: window.innerWidth >= 600 ? 10 : 5 } }
          }}
          pageSizeOptions={[5, 10, 25]}
          checkboxSelection
          onRowSelectionModelChange={newSelectionModel => {
            setSelectedConferences(newSelectionModel)
          }}
          rowSelectionModel={selectedConferences}
          onCellClick={handleCellClick}
          getCellClassName={(params: GridCellParams<ConferenceTest>) => {
            if (params.field === 'title') {
              return 'title--bold'
            } else return ''
          }}
          disableRowSelectionOnClick
        />
      </Box>
    </Card>
  )
}

export default ConferencesTable
